export interface TypeName<T> {
  type: T;
  name: string;
}

export enum MealType {
  BREAKFAST = 'BREAKFAST',
  LUNCH = 'LUNCH',
  DINNER = 'DINNER'
}

export enum FoodType {
  VEGETARIAN = 'VEGETARIAN',
  VEGAN = 'VEGAN'
}

const FOOD_TYPE_DEF: Record<FoodType, string> = {
  [FoodType.VEGETARIAN]: '',
  [FoodType.VEGAN]: ''
};

export enum CookingComplexity {
  EASY = 'EASY',
  MEDIUM = 'MEDIUM',
  HARD = 'HARD'
}

export interface RecipeBase {
  id: string;
  name: string;
  mealType: MealType;
  foodType?: TypeName<FoodType>;
  cookingTime: number;
  cookingComplexity: CookingComplexity;
  imgUrl: string;
}

export interface RecipeFull extends RecipeBase {
  comment: string;
  numberOfServings: number;
  calories: number;
  ingredients: string[];
  preparationSteps: string[];
}

